/*** typography ***/
/* font family */
:root {
    --bs-body-font-family: "Josefin Sans", Arial, sans-serif;
}
h1, h2, h3, h4, h5, h6 {
    font-family: "Cardo", "Times New Roman", serif;
}
.font-primary, 
.font-primary > * {
    font-family: var(--font-primary);
}
.font-secondary, 
.font-secondary > * {
    font-family: var(--font-secondary);
}
/* set base font size */
html {
    font-size: var(--base_font_size);
    color: var(--text);
}
@media screen and (max-width: 768px) {
    html {
        font-size: var(--base_font_size_small);
   }
}
/* font weight */
.weight-300, .weight-300 > * {
    font-weight: 300 !important;
}
.weight-400, .weight-400 > * {
    font-weight: 400 !important;
}
.weight-600, .weight-600 > * {
    font-weight: 600 !important;
}
.weight-800, .weight-800 > * {
    font-weight: bold !important;
}
.weight-900, .weight-900 > * {
    font-weight: 900;
}
/*** colors ***/
.text-primary,
.text-primary * {
    color: var(--primary);
}
.text-secondary,
.text-secondary * {
    color: var(--secondary);
}
.text-success,
.text-success * {
    color: var(--success);
}
.text-text,
.text-text * {
    color: var(--text);
}
.text-gray,
.text-gray * {
    color: var(--gray);
}
.text-white,
.text-white * {
    color: var(--white);
}
/** alignment **/
/* left */
.align-left,
.align-left * {
    text-align: left;
}
.row.align-left,
.column-align-left {
    justify-content: flex-start;
}
.divider.align-left {
    justify-content: flex-start;
    margin-right: auto;
}
/* center */
.align-center,
.align-center * {
    text-align: center;
}
.row.align-center,
.column-align-center  {
    justify-content: center;
}
.divider.align-center  {
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
}
.has-max-width {
    margin-left: auto;
    margin-right: auto;
}
/* right */
.align-right,
.align-right * {
    text-align: right;
}
.row.align-right,
.column-align-right {
    justify-content: flex-end;
}
.divider.align-right {
    justify-content: flex-end;
    margin-left: auto;
}
/* links */
a {
    text-decoration: none;
}
.text-wrapper p a {
    text-decoration: underline;
    color: var(--secondary);
}
.white-text-link p a {
    color: var(--white);
    text-decoration: underline;
}
/*** header ***/
/** menu wrapper **/
/*
#main-nav.fixed-top {
    transition: top 1s ease-in-out;
    opacity: 0;
}
#main-nav.fixed-top {
    box-shadow: 0px 15px 10px -15px rgba(119,122,126,.16) !important;
    -webkit-box-shadow: 0px 15px 10px -15px rgba(119,122,126,.16) !important;
}
*/
/** main menu **/
#main-menu {
    align-items: center;
}
.navbar-nav .nav-link {
    font-weight: 600;
}
.btn.menu-cta {
    padding: 0;
    border-radius: 0;
}
.menu-cta a, .menu-cta:hover a {
    color: #ffffff;
    padding-left: 20px !important;
    padding-right: 20px !important;
}
.navbar-brand img {
    max-width: var(--logo_width);
}
/** top menu **/
.navbar-top {
    background: linear-gradient( to right, #055AA7 0%, #055AA7 40%, #2680d1 40%, #2680d1 75%, #e1f1ff 75%, #e1f1ff 100% );
}
.navbar-top {
    padding: 0;
}
.navbar-top .menu {
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding-left: 0;
    margin-bottom: 0;
}
.navbar-top .menu .menu-item {
    list-style: none;
    font-size: 14px;
    text-align: center;
}
.navbar-top .menu .menu-item {
    transform: skewX(-50deg);
    transition: ease-in-out .2s;
}
.navbar-top .menu .menu-item a {
    color: #ffffff;
    text-decoration: none;
    display: block;
    padding-top: 6px;
    padding-bottom: 6px;
    transform: skewX(50deg);
}
@media screen and (min-width: 1320px) {
    .navbar-top .menu-item {
        width: 30%;
    }
    .navbar-top .menu {
        margin-left: 20%;
        width: 80%;
    }
    .navbar-top .menu .menu-item {
        width: 25%;
    }
    .navbar-top .menu .menu-item:hover {
        width: 30%;
    }
    /*
    .dropdown:hover .dropdown-menu{
        display: block;
    }
    */
    .mobile-menu-item {
        display: none;
    }
}
@media screen and (max-width: 1319px) {
    .navbar-top .menu {
        margin-left: 0;
        width: 100%;
   }
    .navbar-top .menu .menu-item {
        width: 25%;
   }
}
@media screen and (min-width: 991px) {
    .mobile-menu-item {
        display: none;
    }
    .menu-cta {
        margin-left: 20px;
    }
}
@media screen and (max-width: 990px) {
    .navbar-top .container {
        max-width: 100%;
    }
    .navbar-top .menu .menu-item {
        width: 25%;
    }
    .navbar-top .menu .menu-item a {
        transform: none;
    }
    .navbar-top .menu .menu-item {
        transform: none;
    }
    .container, .container-md, .container-sm, body.understrap-no-sidebar .alignfull .wp-block-cover__inner-container, body.understrap-no-sidebar .alignwide .wp-block-cover__inner-container, body.understrap-no-sidebar .wp-block-cover.alignfull .wp-block-cover__inner-container, body.understrap-no-sidebar .wp-block-cover.alignwide .wp-block-cover__inner-container {
        max-width: unset;
    }
    #main-menu {
        align-items: flex-start;
        padding-right: 0 !important;
    }
    #main-menu li {
        width: 100%;
    }
    .offcanvas-body {
        padding-top: 0;
    }
    .menu-cta {
        margin-top: 20px;
    }
    .dropdown-menu {
        margin-bottom: 20px;
    }
    .navbar-toggler {
        background: var(--light);
    }
}
@media screen and (max-width: 768px) {
    .navbar-top .container {
        padding-left: 0;
        padding-right: 0;
   }
    #menu-phone-numbers {
        flex-wrap: wrap;
   }
    .navbar-top .menu .menu-item {
        width: 50%;
   }
}
.navbar-top .phone-1 {
    background: #055aa7;
}
.navbar-top .phone-1:hover {
    background: #0C76D5;
}
.navbar-top .phone-2 {
    background: #2680d1;
}
.navbar-top .phone-2:hover {
    background: #4995D9;
}
.navbar-top .phone-3 {
    background: #399ffc;
}
.navbar-top .phone-3:hover {
    background: #62B3FC;
}
.navbar-top .phone-4 {
    background: #e1f1ff;
}
.navbar-top .phone-4:hover {
    background: #ffffff;
}
.navbar-top .menu .menu-item.phone-4 a {
    color: #055aa7;
}
.fa-icon-container {
    display: inline;
    padding-right: 8px;
}
/*** layouts ***/
/** responsive display **/
@media screen and (max-width: 990px) {
    .hide-on-mobile {
        display: none;
    }
}
/* wrapper */
.wrapper {
    padding-top: 0;
    padding-bottom: 0;
}
/** containers **/
.wrapper .container-fluid, 
.wrapper .container {
    position: relative;
}
/* apply flex to containers and rows that use it */
.wrapper .container-fluid.has-flex-vertical-align, 
.wrapper .container.has-flex-vertical-align,
.wrapper .container-fluid.has-flex-vertical-align .row, 
.wrapper .container.has-flex-vertical-align .row,
.wrapper .container-fluid.has-flex-horizontal-align, 
.wrapper .container.has-flex-horizontal-align,
.wrapper .container-fluid.has-flex-horizontal-align .row, 
.wrapper .container.has-flex-horizontal-align .row {
    display: flex;
}
.edit-link {
    display: none;
}
/* remove min-height on mobile */
@media screen and (max-width: 768px) {
    .has-min-height {
        min-height: unset !important;
    }
}
/** rows **/
.wrapper .container-fluid .row.wp-block-cover__inner-container {
    max-width: 100%;
    padding-left: 0;
    padding-right: 0;
}
.wrapper .row {
    position: relative;
}
/** columns **/
.col-inner {
    height: 100%;
    display: flex;
    flex-direction: column;
    position: relative;
    z-index: 1;
}
.col-inner > .element {
    display: block;
}
.col-inner-content {
    height: 100%;
    display: flex;
    flex-direction: column;
    position: relative;
    z-index: 1;
}
.col-inner-content > .element {
    display: block;
}
.even-columns .col-inner-content > .element:last-child {
    margin-top: auto;
}
.col-inner-content {
    z-index: 2;
}
/* vertical half section */
.vertical-half-section {
    padding-left: 0;
    padding-right: 0;
}
.vertical-half-section .row {
    margin-top: auto;
    background: rgba(255, 255, 255, .8);
    width: 100%;
}
.vertical-half-section .row .col-inner {
    max-width: 1000px;
    margin-left: auto;
    margin-right: auto;
    padding-top: 5rem;
    padding-bottom: 5rem;
}
/* spacing */
.no-margin-bottom *:last-of-type {
    margin-bottom: 0;
}
/* video background */
.video-bg {
    position: relative;
    width: 100%;
    overflow: hidden;
    display: flex;
    align-items: center;
}
.video-bg video {
    position: absolute;
    top: 50%;
    left: 50%;
    min-width: 100%;
    min-height: 100%;
    width: auto;
    height: auto;
    z-index: 0;
    transform: translateX(-50%) translateY(-50%);
}
.video-bg .inner-container {
    position: relative;
    z-index: 2;
}
.video-bg .row {
    z-index: 10;
}
/* overlay */
.overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1;
}
/*** elements ***/
/** buttons **/
/** button group **/
.button-group {
    width: 100%;
}
/** single buttons */
.btn {
    border-radius: var(--button_border-radius) !important;
    text-decoration: none !important;
    text-align: center;
}
.btn-primary, .btn-primary:hover, .btn-primary:focus, .btn-primary:active {
    color: var(--white) !important;
}
.btn-secondary, .btn-secondary:hover, .btn-secondary:focus, .btn-secondary:active {
    color: var(--white) !important;
}
.btn-success, .btn-success:hover, .btn-success:focus, .btn-success:active {
    color: var(--white) !important;
}
/*** button-icons ***/
.button-icon.icon-position-left i {
    margin-right: .5em;
}
.button-icon.icon-position-right i {
    margin-left: .5em;
}
/*** gallery ***/
.image-gallery.element {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
}
@media screen and (max-width: 768px) {
    .image-gallery.element {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
   }
}
/** images **/
.element.img {
    margin-bottom: .75em;
}
/*** dividers ***/
/* divider element */
.element.divider {
    margin-bottom: 1rem;
}
/* left and right dividers - diagonal line */
@media screen and (min-width: 981px) {
    .divider-diagonal.divider-right-to-left.divider-col-1 .has-background:nth-child(1) .col-inner {
        -webkit-clip-path: polygon(0 0, 100% 0, 85% 100%, 0% 100%);
        clip-path: polygon(0 0, 100% 0, 85% 100%, 0% 100%);
    }
    .divider-diagonal.divider-left-to-right.divider-col-2 .has-background:nth-child(2) .col-inner {
        -webkit-clip-path: polygon(0 0, 100% 0%, 100% 100%, 15% 100%);
        clip-path: polygon(0 0, 100% 0%, 100% 100%, 15% 100%);
    }
}
@media screen and (min-width: 981px) {
    .divider-right-to-left {
        clip-path: polygon(0 0, 100% 0%, 85% 100%, 0 100%);
    }
    .divider-left-to-right {
        clip-path: polygon(0 0, 100% 0%, 100% 100%, 15% 100%);
    }
}
/* top and bottom dividers - wave */
/** testimonials carousel **/
.testimonials-carousel .carousel-inner {
    width: 150%;
    left: -25%;
}
.testimonials-carousel .carousel-content {
    display: flex;
}
/* .carousel-item-overlay {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background: rgba(255, 255, 255, .7);
}
.carousel-item.active .carousel-item-overlay {
    background: rgba(255, 255, 255, 0);
}
*/
.testimonials-carousel .carousel-inner-overlay {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1;
    background-image: linear-gradient( to left, rgba(255, 255, 255, .7) 0%, rgba(255, 255, 255, .7) 33%, rgba(255, 255, 255, 0) 33%, rgba(255, 255, 255, 0) 67%, rgba(255, 255, 255, .7) 67%, rgba(255, 255, 255, .7) 100% );
}
@media (max-width: 767px) {
    .carousel-inner .carousel-item > div {
        display: none;
   }
    .carousel-inner .carousel-item > div:first-child {
        display: block;
   }
}
.testimonials-carousel .carousel-control-next, .testimonials-carousel .carousel-control-prev {
    width: 32%;
}
.carousel-inner .carousel-item.active, .carousel-inner .carousel-item-next, .carousel-inner .carousel-item-prev {
    display: flex;
}
/* medium and up screens */
@media (min-width: 768px) {
    .carousel-inner .carousel-item-end.active, .carousel-inner .carousel-item-next {
        transform: translateX(33.33%);
   }
    .carousel-inner .carousel-item-start.active, .carousel-inner .carousel-item-prev {
        transform: translateX(-33.33%);
   }
}
.carousel-inner .carousel-item-end, .carousel-inner .carousel-item-start {
    transform: translateX(0);
}
.carousel-indicators {
    bottom: -80px;
}
.carousel-dark .carousel-indicators [data-bs-target] {
    background-color: var(--secondary);
    height: 4px;
    margin-right: 6px;
    margin-left: 6px;
}
/** post carousel **/
.post-carousel .card {
    border: 0;
    border-radius: 0;
}
/*** accordions ***/
/** icon **/
.accordion-button.collapsed::after {
    background-image: none;
    font-family: "Font Awesome 5 Pro";
    font-weight: 400;
    content: "\f077";
    font-size: 22px;
}
.accordion-button:not(.collapsed)::after {
    background-image: none;
    font-family: "Font Awesome 5 Pro";
    font-weight: 400;
    content: "\f077";
    font-size: 22px;
}
.accordion-button:focus {
    border-color: none;
    box-shadow: none;
}
.accordion-item {
    border-top-left-radius: 0 !important;
    border-top-right-radius: 0 !important;
    border-bottom-left-radius: 0 !important;
    border-bottom-right-radius: 0 !important;
}
/** heading colors **/
/* reset bootstrap sass */
.accordion-font-size .accordion-button {
    font-size: inherit;
}
.accordion-item:first-of-type .accordion-button {
    border-top-left-radius: var(--button_border-radius);
    border-top-right-radius: var(--button_border-radius);
}
/* primary */
.text-open-primary .accordion-button:not(.collapsed) {
    color: var(--primary);
}
.text-collapsed-primary .accordion-button.collapsed {
    color: var(--primary);
}
.bg-open-primary .accordion-button:not(.collapsed) {
    background-color: var(--primary);
}
.bg-collapsed-primary .accordion-button.collapsed {
    background-color: var(--primary);
}
/* secondary */
.text-open-secondary .accordion-button:not(.collapsed) {
    color: var(--secondary);
}
.text-collapsed-secondary .accordion-button.collapsed {
    color: var(--secondary);
}
.bg-open-secondary .accordion-button:not(.collapsed) {
    background-color: var(--secondary);
}
.bg-collapsed-secondary .accordion-button.collapsed {
    background-color: var(--secondary);
}
/* accent */
.text-open-accent .accordion-button:not(.collapsed) {
    color: var(--accent);
}
.text-collapsed-accent .accordion-button.collapsed {
    color: var(--accent);
}
.bg-open-accent .accordion-button:not(.collapsed) {
    background-color: var(--accent);
}
.bg-collapsed-accent .accordion-button.collapsed {
    background-color: var(--accent);
}
/* text */
.text-open-dark .accordion-button:not(.collapsed) {
    color: var(--text);
}
.bg-open-dark .accordion-button:not(.collapsed) {
    background-color: var(--text);
}
.bg-collapsed-dark .accordion-button.collapsed {
    background-color: var(--text);
}
/* light */
.text-open-light .accordion-button:not(.collapsed) {
    color: var(--light);
}
.text-collapsed-light .accordion-button.collapsed {
    color: var(--light);
}
.bg-open-light .accordion-button:not(.collapsed) {
    background-color: var(--light);
}
.bg-collapsed-light .accordion-button.collapsed {
    background-color: var(--light);
}
/* white */
.text-open-white .accordion-button:not(.collapsed) {
    color: var(--white);
}
.text-collapsed-white .accordion-button.collapsed {
    color: var(--white);
}
.bg-open-white .accordion-button:not(.collapsed) {
    background-color: var(--white);
}
.bg-collapsed-white .accordion-button.collapsed {
    background-color: var(--white);
}
/** icon lists **/
/* .icon-list {
    margin-left: 0;
} */
.icon-list {
    padding-left: 0;
}
.icon-list li {
    display: flex;
    align-items: flex-start;
}
.icon-list li i {
    display: block;
    margin-right: 10px;
}
.fa-ul.icon-blue {
    margin: 0;
}
.fa-ul.icon-blue li {
    padding-top: .25rem;
    padding-bottom: .25rem;
}
.icon-blue i {
    margin-right: 10px;
}
.icon-blue i:before {
    content: "\f058";
    font-size: 30px;
    color: var(--primary);
}
.list-inline ul {
    display: flex;
    padding-left: 0;
    margin-bottom: 0;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    list-style: none;
    margin-top: 12px;
}
.list-three-columns li {
    width: 33.3%;
    text-align: left;
    display: flex;
    margin-bottom: 12px;
    padding-right: 20px;
}
.list-bullet-primary i {
    color: #055aa7;
    margin-right: 10px;
    top: 2px;
    position: relative;
}
.icon-list.orientation-horizontal {
    display: flex;
    margin-left: auto;
    margin-right: auto;
}
.icon-list-small-text h3 {
    font-size: 1rem;
    font-family: var(--font-primary);
    font-weight: bold;
}
/*** sidebar ***/
/** sidebar layout **/
.page-template-right-sidebarpage .page-title,
.page-template-right-sidebarpage #wrapper-navbar {
    z-index: 2;
}
.page-template-right-sidebarpage .site-main > .row {
    display: flex;
}
@media screen and (min-width: 992px) {
    .page-template-right-sidebarpage .site-main > .row {
        flex-direction: row;
    }
}
@media screen and (max-width: 991px) {
    .page-template-right-sidebarpage .site-main > .row {
        flex-direction: column-reverse;
    }
}
/** table of contents **/
#right-sidebar {
    padding-left: 1rem;
    padding-right: 1rem;
}
#right-sidebar .widget_block {
    padding-top: 3rem;
    padding-bottom: 0;
    max-width: calc(100vw - 2rem);
    margin-left: auto;
    margin-right: auto;
    color: var(--primary);
}
@media screen and (min-width: 990px) {
    .widget-area.sticky {
        z-index: 0;
    }
}
.toc {
    max-width: calc(100vw - 2rem);
    margin-left: auto;
    margin-right: auto;
    margin-bottom: -1rem;
}
.toc-title {
    font-size: 1.5rem;
}
.toc-list {
    background: #F6F6F6;
    padding: 2rem 1.5rem 2rem 2.5rem;
}
.toc li::marker {
    color: #055aa7;
}
.toc-list a {
    color: var(--text);
}
#right-sidebar .widget_block p:first-child,
#right-sidebar .widget_block p:last-child {
    display: none;
    margin: 0;
}
@media screen and (min-width: 991px) {
    .sticky-toc {
        position: fixed;
        top: 0;
        right: 0;
        transition: ease-in-out .2s;
        /* margin-top: 75px; */
   }
}
/** cards **/
.card a {
    color: var(--text);
}
.card .card-title {
    color: var(--primary);
}
.card,
.card-body {
    height: 100%;
    display: flex;
    flex-direction: column;
    color: var(--text);
}
.card-body p:last-child {
    margin-top: auto;
    margin-bottom: 0;
}
.card-body a:first-child {
    margin-bottom: 1em;
}
/*** footer ***/
#footer-full-content,
#wrapper-footer {
    max-width: 100% !important;
    z-index: 9;
}
#wrapper-footer-full .hours {
    max-width: 720px;
    margin-left: auto;
    margin-right: auto;
}
#wrapper-footer-full .hours ul {
    padding-left: 0;
    list-style: none;
}
.footer-locations .col-inner-content h2 {
    max-width: 700px;
    margin-left: auto;
    margin-right: auto;
}
@media screen and (min-width: 768px) {
    .site-info {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
   }
    .footer-menu ul li {
        list-style: none;
        display: inline;
   }
}
.footer-menu ul {
    margin-top: .25rem;
    margin-bottom: .25rem;
    padding: 0;
}
.footer-menu ul li {
    margin-right: 1.5rem;
}
.site-info a:hover {
    color: inherit;
    opacity: .75;
}

/** remove multiple block sizing **/
#wrapper-footer-full .footer-widget {
    width: 100%;
}

/** footer map **/
/* iframes */
.temporary-hide {
    display: none !important;
}
iframe {
    display: block;
}
.footer-widget.widget_block {
    padding: 0;
}
.footer-locations-map .row {
    background: rgba(240, 247, 254, 0.75);
    padding-top: 3rem;
    padding-bottom: 3rem;
}
.footer-locations-map .col-inner-content .element:last-child {
    margin-top: auto;
}
.footer-locations-map .btn {
    margin-top: 0 !important;
}
@media screen and (min-width: 1300px) {
    .footer-locations-map .row {
        width: 55% !important;
        padding-left: 5% !important;
        margin-left: auto !important;
        margin-right: 0 !important;
        -webkit-clip-path: polygon(10% 0, 100% 0%, 100% 100%, 0% 100%) !important;
        clip-path: polygon(10% 0, 100% 0%, 100% 100%, 0% 100%) !important;
    }
}
@media screen and (max-width: 1299px) {
    .footer-locations-map .row {
        width: 75% !important;
        padding-left: 5% !important;
        margin-left: auto !important;
        margin-right: 0 !important;
        -webkit-clip-path: polygon(10% 0, 100% 0%, 100% 100%, 0% 100%) !important;
        clip-path: polygon(10% 0, 100% 0%, 100% 100%, 0% 100%) !important;
    }
}
@media screen and (max-width: 980px) {
    .footer-locations-map .row {
        width: 100% !important;
        padding-left: 0 !important;
        margin-left: 0 !important;
        margin-right: 0 !important;
        -webkit-clip-path: none !important;
        clip-path: none !important;
    }
}

/** footer bottom **/
/* menus */
/* icons */
.footer-social-icon {
    font-size: 1.25rem;
}
.footer-social-icon.separator-top {
    margin-top: 1.25rem;
}
.footer-social-icon.separator-right {
    margin-right: 1.25rem;
}
.footer-social-icon.separator-bottom {
    margin-bottom: 1.25rem;
}
.footer-social-icon.separator-left {
    margin-left: 1.25rem;
}
/* responsive */
@media screen and (max-width: 768px) {
    .footer-menu {
        margin-bottom: 1.5rem;
    }
    .footer-menu ul {
        columns: 2;
        -webkit-columns: 2;
        -moz-columns: 2;
        list-style: none;
        padding-left: 0;
        text-align: left;
    }
    .footer-copyright {
        margin-bottom: 1.5rem;
        text-align: left;
    }
    .footer-social {
        text-align: left;
    }
}
/**** pages ****/
/*** locations pages ***/
.two-columns-white-bg .row {
    background: #ffffff;
}
.two-columns-white-bg .img.element {
    margin-bottom: 0 !important;
}
.darker-light-blue-bg .overlay {
    background-color: #C7E1F9;
}
.map-links a {
    display: inline-block;
    margin-left: .5rem;
    margin-right: .5rem;
}
.map-links a i {
    height: 45px;
    width: auto;
}
/*** blogs ***/
/** archive **/
.ajax-load-more-wrap {
    margin-top: 3rem !important;
    margin-bottom: 3rem !important;
}
.alm-listing .alm-item img {
    display: block;
    width: 100%;
    max-width: 100% !important;
    position: relative !important;
}
.alm-reveal {
    display: inline-flex;
    flex-wrap: wrap;
    gap: 2%;
}
.alm-reveal .col-element {
    width: 31%;
}
@media screen and (max-width: 990px) {
    .alm-reveal .col-element {
        width: 49%;
   }
}
@media screen and (max-width: 768px) {
    .alm-reveal .col-element {
        width: 100%;
   }
}
.alm-reveal .post-content {
    padding: 1.5rem;
}
.alm-reveal .post-content h3 {
    font-size: 1.4rem;
}
.alm-reveal .excerpt p {
    margin-bottom: 0;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
}
.read-more {
    margin: auto 0 1.5rem 1.5rem;
}
.alm-load-more-btn {
    background: var(--primary) !important;
}
a.col-element,
a.col-inner {
    text-decoration: none;
    color: inherit;
}
/** single **/
.post-template-default .entry-content {
    margin: 2rem;
    padding: 2rem;
    background: var(--light);
}
.post-template-default .post-navigation {
    padding-left: 2rem;
    padding-right: 2rem;
    margin-bottom: 3rem;
}
.post-template-default .post-date {
    display: block;
    font-weight: bold;
    font-size: 1.15rem;
    margin-bottom: 1rem;
}

/*** forms ***/
.gform_wrapper .gform_heading {
    display: none;
}
/** padding **/
.gform_wrapper  input,
.gform_wrapper  .gfield_select,
.gform_wrapper  textarea {
    padding: .5rem !important;
    border-width: 1px;
    border-radius: var(--button_border-radius);
}
.gform_wrapper  input:focus,
.gform_wrapper  .gfield_select:focus,
.gform_wrapper  textarea:focus {
    border-color: var(--secondary);
    box-shadow: none;
    outline: none;
    border-style: solid;
    border-width: 1px;
    border-radius: var(--button_border-radius);
}
/** placeholder text **/
/* input fields */
.gform_wrapper ::-webkit-input-placeholder { /* Edge */
    color: var(--text);
    opacity: .7;
}
.gform_wrapper :-ms-input-placeholder { /* Internet Explorer 10-11 */
    color: var(--text);
    opacity: .7;
}
.gform_wrapper ::placeholder {
    color: var(--text);
    opacity: .7;
}
/* select field */
.gform_wrapper .gfield_select {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    color: var(--text);
    opacity: .7;
    border: 1px solid var(--text);
    border-radius: var(--button_border-radius);
}

/** back to top button **/
#btn-back-to-top {
    display: none;
    position: fixed;
    bottom: 80px;
    right: 18px;
    border-radius: 100px !important;
    align-items: center;
    justify-content: center;
    background: rgba(75,75,75,.6);
    height: 40px;
    width: 40px;
    color: #fff;
    font-size: 20px;
    z-index: 9;
}
#btn-back-to-top i {
    top: 1px;
    position: relative;
}